import React, { useEffect, useState } from "react";
import { getSpecies } from "../../api/api";
import Preloader from "../common/Preloader";
import s from "./Species.module.css";

const Species = (props) => {
  const [count, setCount] = useState(1);
  const [specimen, setSpecimen] = useState();

  useEffect(() => {
    getSpecies(count)
      .then((res) => {
        // debugger
        setSpecimen(res);
      })
      .catch(console.error);
  }, [count]);

  const getNextSpecimen = () => {
    if (count === 37) {
      setSpecimen(undefined);
      return setCount(1);
    }
    setSpecimen(undefined);
    setCount(count + 1);
  };

  const getPrevSpecimen = () => {
    if (count === 1) {
      setSpecimen(undefined);
      return setCount(37);
    }
    setSpecimen(undefined);
    setCount(count - 1);
  };

  return (
    <div>
      {!specimen ? (
        <div className={s.preloaderWrapper}>
          <Preloader />
        </div>
      ) : (
        <div className={s.contentWrapper}>
          <div>
            <div className={s.leftBlockContent}>
              <img
                src={`https://starwars-visualguide.com/assets/img/species/${count}.jpg`}
                alt="Item"
              ></img>
              <div className={s.contentButtons}>
                <button onClick={getPrevSpecimen}>PREV</button>
                <button onClick={getNextSpecimen}>NEXT</button>
              </div>
            </div>
          </div>
          <div className={s.itemDescription}>
            <div className={s.itemDescriptionWrapper}>
              <h1>{specimen.name}</h1>
              <ul>
                <li>Classification: {specimen && specimen.classification}</li>
                <li>Designation: {specimen && specimen.designation}</li>
                <li>Language: {specimen && specimen.language}</li>
                <li>Avg Lifespan: {specimen && specimen.average_lifespan}</li>
                <li>Avg Height: {specimen && specimen.average_height}</li>
                <li>Skin Color(s): {specimen && specimen.skin_colors}</li>
              </ul>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Species;

