import React from "react";
import { NavLink } from "react-router-dom";
import s from "./Navbar.module.css";

const Navbar = () => {
  return (
    <div className={s.navbar}>
      <NavLink to="/star-wars-react/people">People</NavLink>
      <NavLink to="/star-wars-react/planets">Planets</NavLink>
      <NavLink to="/star-wars-react/species">Species</NavLink>
    </div>
  );
};

export default Navbar;
