import React from 'react';
import { Route, Switch } from 'react-router-dom';
import s from './Content.module.css';
import People from '../People/People';
import Planets from '../Planets/Planets';
import Species from '../Ships/Species';
import WelcomePage from '../WelcomePage/WelcomePage';


const Content = (props) => {
  return <div className={s.content}>
    <Switch />
      <Route exact path='/star-wars-react' render={() => <WelcomePage />} />
      <Route exact path='/star-wars-react/people' render={() => <People />} />
      <Route exact path='/star-wars-react/planets' render={() => <Planets />} />
      <Route exact path='/star-wars-react/species' render={() => <Species />} />
    <Switch />
  </div>
}



export default Content