import React, { useEffect, useState } from "react";
import { getPeople } from "../../api/api";
import Preloader from "../common/Preloader";
import s from "./People.module.css";

const People = (props) => {
  const [count, setCount] = useState(1);
  const [person, setPerson] = useState();

  useEffect(() => {
    getPeople(count)
      .then((res) => {
        // debugger
        setPerson(res);
      })
      .catch(console.error);
  }, [count]);

  const getNextPerson = () => {
    if (count === 83) {
      setPerson(undefined);
      return setCount(1);
    }
    setPerson(undefined);
    setCount(count + 1);
  };

  const getPrevPerson = () => {
    if (count === 1) {
      setPerson(undefined);
      return setCount(83);
    }
    setPerson(undefined);
    setCount(count - 1);
  };

  return (
    <div>
      {!person ? (
        <div className={s.preloaderWrapper}>
          <Preloader />
        </div>
      ) : (
        <div className={s.contentWrapper}>
          <div>
            <div className={s.leftBlockContent}>
              <div className={s.leftBlockContentImgWrapper}>
                <img
                  src={`https://starwars-visualguide.com/assets/img/characters/${count}.jpg`}
                  alt="Item"
                ></img>
              </div>
              <div className={s.contentButtons}>
                <button onClick={getPrevPerson}>PREV</button>
                <button onClick={getNextPerson}>NEXT</button>
              </div>
            </div>
          </div>
          <div className={s.itemDescription}>
            <div className={s.itemDescriptionWrapper}>
              <h1>{person.name}</h1>
              <ul>
                <li>Birth Year: {person && person.birth_year}</li>
                <li>Height: {person && person.height}</li>
                <li>Mass: {person && person.mass}</li>
                <li>Gender: {person && person.gender}</li>
                <li>Hair Color: {person && person.hair_color}</li>
                <li>Skin Color: {person && person.skin_color}</li>
              </ul>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default People;

