import React, { useEffect, useState } from "react";
import { getPlanets } from "../../api/api";
import Preloader from "../common/Preloader";
import s from "./Planets.module.css";

const Planets = (props) => {
  const [count, setCount] = useState(2);
  const [planet, setPlanet] = useState();

  useEffect(() => {
    getPlanets(count)
      .then((res) => {
        // debugger;
        setPlanet(res);
      })
      .catch(console.error);
  }, [count]);

  const getNextPlanet = () => {
    if (count === 21) {
      setPlanet(undefined);
      return setCount(2);
    }
    setPlanet(undefined);
    setCount(count + 1);
  };

  const getPrevPlanet = () => {
    if (count === 2) {
      setPlanet(undefined);
      return setCount(21);
    }
    setPlanet(undefined);
    setCount(count - 1);
  };

  return (
    <div>
      {!planet ? (
        <div className={s.preloaderWrapper}>
          <Preloader />
        </div>
      ) : (
        <div className={s.contentWrapper}>
          <div>
            <div className={s.leftBlockContent}>
              <img
                src={`https://starwars-visualguide.com/assets/img/planets/${count}.jpg`}
                alt="Item"
              ></img>
              <div className={s.contentButtons}>
                <button onClick={getPrevPlanet}>PREV</button>
                <button onClick={getNextPlanet}>NEXT</button>
              </div>
            </div>
          </div>
          <div className={s.itemDescription}>
            <div className={s.itemDescriptionWrapper}>
              <h1>{planet.name}</h1>
              <ul>
                <li>Population: {planet && planet.population}</li>
                <li>Diameter: {planet && planet.diameter}</li>
                <li>Gravity: {planet && planet.gravity}</li>
                <li>Terrain: {planet && planet.terrain}</li>
                <li>Surface Water: {planet && planet.surface_water}</li>
                <li>Climate: {planet && planet.climate}</li>
              </ul>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Planets;

