import React from 'react';
import preloader from "./../../img/bar.gif"
import Background from './../../img/background-space.jfif'

let Preloader = (props) => {
  return <div  style={ {backgroundImage: "url(" + {Background} + ")"}}>
      <img src={preloader} alt="preloader" style={{display: 'block', margin: '0 auto'}}/>
  </div>
}

export default Preloader;